@font-face {
  font-family: 'RobotoNormal';
  src: url(/tesla-range-calculator/static/media/Roboto-Regular-webfont.30799efa.eot);
  src: url(/tesla-range-calculator/static/media/Roboto-Regular-webfont.30799efa.eot?#iefix) format('embedded-opentype'),
       url(/tesla-range-calculator/static/media/Roboto-Regular-webfont.3e5675c8.woff) format('woff'),
       url(/tesla-range-calculator/static/media/Roboto-Regular-webfont.7f1320f7.ttf) format('truetype'),
       url(/tesla-range-calculator/static/media/Roboto-Regular-webfont.1d45bc40.svg#RobotoRegular) format('svg');
  font-weight: normal;
  font-style: normal;
}
*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font: 300 14px/1.4 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}
.cf:before,
.cf:after {
    content: '';
    display: table;
}
.cf:after {
    clear: both;
}
.cf {
  *zoom: 1;
}
.wrapper {
  margin: 100px 0 150px;
}

.header {
  padding: 25px 0;
  text-align: center;
  background: #222;
}
.header img {
  width: 100px;
  height: 13px;
}

.tesla-battery {
  width: 1050px;
  margin: 0 auto;
}
.tesla-battery h1 {
  font-family: 'RobotoNormal';
  font-weight: 100;
  font-size: 38px;
  text-align: center;
  letter-spacing: 3px;
}
.tesla-car {
  width: 100%;
  min-height: 350px;
  background: #fff url(/tesla-range-calculator/static/media/tesla.ff16203f.jpg) no-repeat top center;
  background-size: contain;
}

.tesla-wheels {
  height: 247px;
  width: 555px;
  position: relative;
  margin: 0 auto;
}

.tesla-wheel {
  height: 80px;
  width: 80px;
  bottom: 0;
  position: absolute;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;
}
.tesla-wheel--front {
  left: 53px;
}
.tesla-wheel--rear {
  right: 72px;
}
.tesla-wheel--19 {
  background-image: url(/tesla-range-calculator/static/media/wheel-19.3b975a00.png);
  -webkit-animation: infinite-spinning 250ms steps(6) infinite;
  animation: infinite-spinning 250ms steps(6) infinite;
}
.tesla-wheel--21 {
  background-image: url(/tesla-range-calculator/static/media/wheel-21.e787dc92.png);
  -webkit-animation: infinite-spinning 480ms steps(12) infinite;
  animation: infinite-spinning 480ms steps(12) infinite;
}

@keyframes infinite-spinning {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes infinite-spinning {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tesla-battery__notice {
    margin: 20px 0;
    font-size: 15px;
    color: #666;
    line-height: 20px;
}

.tesla-stats {
  margin: -70px 0 30px; }
  .tesla-stats ul {
    text-align: center; }
    .tesla-stats ul li {
      display: inline-block;
      width: 130px;
      position: relative; }
      .tesla-stats ul li p {
        font-size: 40px;
        font-weight: normal;
        font-family: 'RobotoNormal';
        display: block;
        padding: 0 18px 0 0;
        position: relative;
        color: #008dff;
        text-align: right; }
        .tesla-stats ul li p:after {
          font-size: 14px;
          font-weight: normal;
          font-family: 'RobotoNormal';
          content: 'MI';
          position: absolute;
          top: 8px;
          right: 0; }
  .tesla-stats-icon {
    height: 20px;
    background-size: auto 13px;
    background-position: top right;
    background-repeat: no-repeat; }
    .tesla-stats-icon--60 {
      background-image: url(/tesla-range-calculator/static/media/60.7ee66eea.svg); }
    .tesla-stats-icon--60d {
      background-image: url(/tesla-range-calculator/static/media/60d.422a1e93.svg); }
    .tesla-stats-icon--75 {
      background-image: url(/tesla-range-calculator/static/media/75.37862a62.svg); }
    .tesla-stats-icon--75d {
      background-image: url(/tesla-range-calculator/static/media/75d.0c765a24.svg); }
    .tesla-stats-icon--90d {
      background-image: url(/tesla-range-calculator/static/media/90d.0fbc4c50.svg); }
    .tesla-stats-icon--p100d {
      background-image: url(/tesla-range-calculator/static/media/p100d.a2f30d07.svg); }
